<template>
	<div>
		<div class="title">欢迎使用自助服务终端</div>
		<div class="kd-page-content-wrapper wrap">
			<div class="left">
				<div class="item" v-for="(item, index) in INDEX_MENU" :key="index" :style="itemStyle(item, index)" :class="{'activeMenu': activeMenu === index}" @click="changeMenu(index)">
					<div class="littleShapeTop" v-if="activeMenu !== index"></div>
					<div class="littleShapeBottom" v-if="activeMenu !== index"></div>
					<div class="itemWrap">
						<div v-if="activeMenu === index" :class="{'imgActiveWrap': activeMenu === index}" :style="{background: activeMenu !== index ? '' : item.background}">
							<img :src="item.icon" alt=""/>
						</div>
						<img v-else :src="item.icon" alt=""/>
						<p>{{ item.name }}</p>
					</div>
				</div>
			</div>
			<div class="right">
				<div class="pageItems">
					<div class="itemSingle" v-for="(item, index) in currentPageItems" :key="index" :style="itemSingleStyle(item, index)" @click="gotoRoute(item)">
						<span class="kedaIconfont" :class="item.icon"></span>
						<span>{{ item.name }}</span>
						<span>{{ !noWarnText && item.disabledBusiness ? '（正在维护中，敬请期待！）' : '' }}</span>
					</div>
				</div>

				<div class="pageControl">
					<div :class="currentPage <= 1 ? 'disablePage' : ''" @click="changePageItems(-1)"><i class="kedaIconfont kedaIcon-solidleft"></i><span>上一页</span></div>
					<div :class="currentPage >= totalPage ? 'disablePage' : ''" @click="changePageItems(1)"><i class="kedaIconfont kedaIcon-solidright"></i><span>下一页</span></div>
				</div>

			</div>
		</div>

	</div>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex';
import { changeRouter } from '@/utils';

export default {
    name: 'MenuIndexN',
    data () {
        return {
            activeMenu: 0,
            totalPage: 0,
            pageSize: 9,
            currentPage: 1,
            currentPageItems: [],
            noWarnText: Boolean(window.localStorage.getItem('noWarnText'))
        };
    },
    computed: {
        ...mapState('permanent', {
            INDEX_MENU: 'INDEX_MENU'
        })
    },
    watch: {
        INDEX_MENU () {
            this.changeMenu(0);
        }
    },
    mounted () {
        this.initData();
        this.changeMenu(0);
    },
    methods: {
        ...mapMutations('common', ['setCurrentBusinessHandle', 'setCurrentBusinessName', 'changeVisible']),
        ...mapActions('common', ['businessStart']),
        itemStyle (item, index) {
            let background = '';
            let borderBottom = '1px solid #ffffff';
            let borderTop = '1px solid #ffffff';
            if (this.activeMenu !== index) {
                background = item.background;
                if (index === 0) {
                    borderTop = '0';
                } else if (index === (this.INDEX_MENU.length - 1)) {
                    borderBottom = '0';
                }
            } else {
                borderBottom = 0;
                borderTop = 0;
            }
            return { background, borderBottom, borderTop };
        },
        itemSingleStyle (item, index) {
            return {};
        },
        gotoRoute (item) {
            if (item.disabledBusiness) {
                return;
            }
            this.setCurrentBusinessHandle(item.code);
            this.setCurrentBusinessName(item.name);
            // todo 测试用
            if (this.CONSTANT.disableHandRequest) {
                this.changeVisible({ visibleSteps: true, visibleTip: false, visibleBusinessName: true, visibleJurisdiction: true });
                changeRouter('/' + item.code);
            } else {
                // todo 社保和业务指南直接进
                if (item.code === 701 || item.code === 801 || item.code === 508) {
                    changeRouter('/' + item.code);
                } else {
                    this.businessStart({ path: '/' + item.code });
                }
            }
        },
        changeMenu (index) {
            this.activeMenu = index;
            this.totalPage = Math.ceil(this.INDEX_MENU[this.activeMenu].menu.length / this.pageSize);
            this.currentPage = 1;
            this.changePageItems();
        },
        changePageItems (type) {
            const originList = this.INDEX_MENU[this.activeMenu].menu;
            let page = this.currentPage;
            if (type === -1) {
                page = page - 1;
                if (page < 1) {
                    return;
                }
            } else if (type === 1) {
                page = page + 1;
                if (page > this.totalPage) {
                    return;
                }
            }
            this.currentPage = page;
            this.currentPageItems = originList.slice(this.pageSize * (page - 1), this.pageSize * (page - 1) + this.pageSize);
        },
        initData () {
            const initListKey = Object.keys(this.$store.state);
            initListKey.forEach((item) => {
                mapMutations(item, ['initData']).initData.call(this);
            });
        }
    }
};
</script>

<style lang="scss" scoped>
	.title {
		font-size: 40px;
		font-weight: 600;
		color: rgba(255, 255, 255, 1);
		line-height: 67px;
		text-align: center;
		padding: 50px 0 30px;
	}

	.wrap {
		height: 63vh;
		padding: 0;
		display: flex;

		.left {
			width: 240PX; /*no*/
			height: 100%;
			display: flex;
			flex-direction: column;

			.item {
				width: 100%;
				border-right: 1px solid #ffffff;
				flex: 1;
				display: flex;
				justify-content: center;
				align-items: center;
				position: relative;
				overflow: hidden;

				.itemWrap {
					width: 80%;
					height: 100%;
					display: flex;
					justify-content: center;
					flex-direction: column;
					align-items: center;

					> img {
						width: 35%;
					}

					.imgActiveWrap {
						box-shadow: 0 0 0 5PX rgba(255, 255, 255, .4); /*no*/
						width: 60PX; /*no*/
						height: 60PX; /*no*/
						border-radius: 150px;
						display: flex;
						justify-content: center;
						align-items: center;

						img {
							width: 90%;
						}
					}

					p {
						font-size: 18px;
						font-weight: 600;
						margin-top: 5%;
					}
				}
			}

			.activeMenu {
				background: none;
				border-right: 0;
			}

		}

		.right {
			flex: 1;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			box-sizing: border-box;
			padding: 50px 50px 0 50px;

			.pageItems {
				display: flex;
				flex-wrap: wrap;

				.itemSingle {
					width: calc((100% - 40PX) / 3); /*no*/
					height: 13vh;
					background: linear-gradient(180deg, rgba(109, 144, 174, 1) 0%, rgba(60, 104, 139, 1) 100%);
					box-shadow: 0 4px 14px 0 rgba(43, 88, 143, 0.4);
					border-radius: 8px;
					border: 1px solid rgba(255, 255, 255, 1);
					box-sizing: border-box;
					display: flex;
					flex-direction: column;
					justify-content: center;
					align-items: center;
					margin-bottom: 20px;

					span:nth-child(1) {
						font-size: 40px;
					}

					span:nth-child(2) {
						padding-top: 10PX; /*no*/
						font-size: 16px;
						font-weight: 600;
					}

					span:nth-child(3) {
						font-size: 12px;
					}
				}

				.itemSingle:nth-child(2), .itemSingle:nth-child(5), .itemSingle:nth-child(8) {
					margin: 0 20PX; /*no*/
				}

				.itemSingle:nth-child(7), .itemSingle:nth-child(8), .itemSingle:nth-child(9) {
					margin-bottom: 0;
				}
			}

			.pageControl {
				display: flex;
				justify-content: center;
				align-items: center;
				height: 140PX; /*no*/
				> div {
					width: 150PX; /*no*/
					height: 60PX; /*no*/
					background: linear-gradient(180deg, rgba(137, 189, 242, 1) 0%, rgba(79, 153, 225, 1) 100%);
					border-radius: 8px;
					border: 1PX solid #FFFFFF; /*no*/
					box-sizing: border-box;
					box-shadow: inset 0 0 0 1PX #559FE9; /*no*/
					margin: 0 20PX; /*no*/
					display: flex;
					justify-content: center;
					align-items: center;
					span {
						padding-bottom: 3PX; /*no*/
						padding-left: 10px;
					}
				}
				.disablePage {
					background:linear-gradient(180deg,rgba(197,215,234,1) 0%,rgba(165,191,216,1) 100%);
					box-shadow: inset 0 0 0 1PX #9EB2C6; /*no*/
				}
			}

		}
	}

	.littleShapeTop {
		position: absolute;
		left: 0; /*no*/
		top: -72PX; /*no*/
		background: rgba(255, 255, 255, 0.1);
		width: 85PX; /*no*/
		height: 119PX; /*no*/
		transform: rotate(30deg);
		border-radius: 6px;
	}

	.littleShapeBottom {
		position: absolute;
		right: -73PX; /*no*/
		bottom: -31PX; /*no*/
		background: rgba(255, 255, 255, 0.1);
		width: 100PX; /*no*/
		height: 100PX; /*no*/
		transform: rotate(-43deg);
		border-radius: 6px;
	}
</style>
